import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Al-Taj School",
    component: HomeView,
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: function () {
      return import("../views/AboutView.vue");
    },
  },

  // STUDENT
  {
    path: "/student",
    name: "Student",
    component: function () {
      return import("../views/StudentView.vue");
    },
  },

  // LEAVE
  {
    path: "/leave",
    name: "Leave",
    component: function () {
      return import("../views/Leave.vue");
    },
  },

  // STUFF
  {
    path: "/stuff",
    name: "Stuff",
    component: function () {
      return import("../views/stuffs/Stuffs.vue");
    },
  },
  {
    path: "/stuff/super-admin",
    name: "Super Admin",
    component: function () {
      return import("../views/stuffs/SuperAdminList.vue");
    },
  },
  {
    path: "/stuff/super-admin/:id",
    name: "Stuff Details",
    component: function () {
      return import("../views/stuffs/SuperAdmin.vue");
    },
  },

  // DOMINATORY
  {
    path: "/dormitory",
    name: "Dormitory",
    component: function () {
      return import("../views/dormitory/Dormitory.vue");
    },
  },

  {
    path: "/dormitory/add-dormitory",
    name: "Add Dormitory",
    component: function () {
      return import("../views/dormitory/AddDormitory.vue");
    },
  },
  {
    path: "/dormitory/add-room",
    name: "Add Room",
    component: function () {
      return import("../views/dormitory/AddRoom.vue");
    },
  },
  {
    path: "/dormitory/room-list",
    name: "Room List",
    component: function () {
      return import("../views/dormitory/RoomList.vue");
    },
  },

  // ATTENDANCE
  {
    path: "/attendance",
    name: "Attendance",
    component: function () {
      return import("../views/attendance/Attendance.vue");
    },
  },

  // FEES
  {
    path: "/fees",
    name: "Fees",
    component: function () {
      return import("../views/fees/Fees.vue");
    },
  },

  // 404
  {
    path: "*",
    name: "not-found",
    component: function () {
      return import("../views/404.vue");
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
